import { Box, Stepper, Step, StepLabel, Typography, Collapse, Button, useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import { useMyServices } from './useMyServices';

const MyServices = () => {
  const { state, handlers, context } = useMyServices();
  const { serviceState, showDetails } = state;
  const { serviceSteps } = context;

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const activeStep = serviceSteps.findIndex(step => step.key === serviceState);

  return (
    <Box
      sx={{
        maxWidth: 600,
        margin: 'auto',
        p: 2,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      }}
    >
      <Typography variant={isMobile ? "h6" : "h5"} align="center" gutterBottom>
        Seguimiento de Servicio
      </Typography>

      <Stepper
        activeStep={activeStep}
        alternativeLabel={!isMobile} // No label alternativo en mobile
        orientation={isMobile ? 'vertical' : 'horizontal'} // Mobile: apilado
        sx={{
          width: '100%',
          '& .MuiStepLabel-root .Mui-active': {
            color: '#1976d2', // Azul personalizado para el activo
          },
          '& .MuiStepIcon-root.Mui-active': {
            color: '#1976d2', // Ícono azul cuando activo
          },
          '& .MuiStepIcon-root.Mui-completed': {
            color: '#4caf50', // Ícono verde cuando completado
          },
        }}
      >
        {serviceSteps.map((step) => (
          <Step key={step.key}>
            <StepLabel>{step.label}</StepLabel>
          </Step>
        ))}
      </Stepper>

      <Button
        variant="text"
        sx={{ mt: 2 }}
        onClick={handlers.toggleDetails}
      >
        {showDetails ? 'Ocultar detalles' : 'Ver más detalles'}
      </Button>

      <Collapse in={showDetails}>
        <Box sx={{ mt: 2, width: '100%' }}>
          <Typography variant="body1" color="text.secondary">
            Aquí se mostrarán los detalles adicionales del servicio adquirido por el cliente. 
            (Por ejemplo: fecha de solicitud, técnico asignado, instrucciones especiales, etc.)
          </Typography>
        </Box>
      </Collapse>
    </Box>
  );
};

export default MyServices;
