import { useState } from 'react';

export const useMyServices = () => {
  const [serviceState, setServiceState] = useState('COMPLETED');
  const [showDetails, setShowDetails] = useState(false);

  const handlers = {
    setServiceState,
    toggleDetails: () => setShowDetails(prev => !prev),
  };

  const context = {
    serviceSteps: [
      { key: 'PENDING', label: 'Pendiente de toma de servicio' },
      { key: 'IN_PROGRESS', label: 'Servicio en proceso' },
      { key: 'COMPLETED', label: 'Servicio completado' },
      { key: 'CLOSED', label: 'Servicio cerrado' },
    ],
  };

  return { state: { serviceState, showDetails }, handlers, context };
};
